body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerContainer {
    background-color: #4b3267;
    color: #fff;
}

.addButton {
    cursor: pointer;
}
.bdg {
    width: 1.8em;
    height: 1.6em;
    border-radius: 50%;
    background-color: rgb(99, 99, 99);
}

.bdgNumber {
    color: white;
    font-weight: 600;
    padding-bottom: .1em
}
.button {
    font-size: .9em;
    padding: 1.2em .8em;
    background-color: #a9a9a9;

}

.buttonName {
    font-weight: 600;
}

.btnBadge {
    position: relative;
}

.btn1 {
    border-radius: 0;
}

.btn2 {
    border-radius: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.4);
    border-right: 1px solid rgba(0, 0, 0, 0.4);
}

.btn3 {
    border-radius: 0;
}

.buttonName {
    padding: 0 .4em 0 0;
    display: inline;
}



.todoTextField {
    width: 100%;
    background-color: #efefef;
    padding: 0 0 0 .1em;
    border-radius: 5px 0 0 5px;
    padding: .5em;
    font-size: 1.1em
}
.editForm {
    width: 100%;
    height: 1em;
    margin-left: -.2em;
    padding: 0 0 1.4em 0;
    /* border: 1px solid red; */
}

.editTextField {
    width: 74%;
    background-color: #efefef;
    padding: 0 0 0 .1em;
    border-radius: 5px 0 0 5px;
    font-style: italic;
}

.editTextField:focus {
    border: none;
    outline: none;
    box-shadow: none;
    border: 2px solid rgba(0, 0, 0, 0.5);
}

.saveButton {
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    font-size: 1.8rem;
    padding: .5rem 1rem .5rem .7rem;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
}
.item {
    padding: 1.5em .2em 1em .2em;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, .125);
    background-color: #efefef;
    color: rgba(0, 0, 0, 0.6);
}

.item:last-child {
    border-bottom: 1px solid rgba(0, 0, 0, .125) !important;
}

.checkBox {
    margin: .1em 0 0 .1em;
    font-size: 1.7em;
}

.todoTitle {
    /* border: 1px solid green; */
    font-size: 1.6em;
    padding: 0 0 .2em .2em;
    word-break: break-all
}

.icons {
    /* border: 1px solid blue; */
}
.columnReverse {
    display: flex;
    flex-direction: column-reverse;
    overflow: scroll;
    position: relative;
    z-index: 0;
}
.main {
  background-color: #d3d3d3;
  padding: 0;
  height: 100vh;
}

.innerContainer {
  position: relative;
  background-color: #fff;
  box-shadow: 0px 0px 17px 2px rgba(119, 119, 119, 0.5);
}

.todoContainer {
  background-color: #efefef;
  height: 100vh;
  width: 100%;
  overflow: auto;
}

.header {
  position: fixed;
  top: 0;
  /* z-index: 1; */
}

.footer {
  position: fixed;
  bottom: 0;
  /* z-index: 1; */
}

.spacer {
  width: 100%;
  height: 7.3em;
  background-color: red;
}
